.card {
    /* border: 0px rgb(199, 199, 199) solid; */
    border: none;
    background: #2d2d2d;
    border-radius: 20px;
    width: 100%;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 4px 4px 10px #3d3d3d;
            box-shadow: 4px 4px 10px #3d3d3d;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    width: 100%;
    overflow: hidden;
    /* background:rgb(255, 255, 255); */
  }
  
  .card > .img-container > img {
    width: 100%;
  }
  
  .card:hover {
    -webkit-box-shadow: 8px 8px 20px #0d0d0d;
            box-shadow: 8px 8px 20px #0d0d0d;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }


